import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
		}
	},
	computed:{
		...mapState(["currentAddress", "devices"])
	},
	onLoad() {
		console.log("onLoad")
		uni.setNavigationBarTitle({
		　　title:'智能硬件'
		})
	},
	onHide(){
		console.log("onHide")
	},
	onShow(){
		console.log("onShow")
	},
	mounted(){
		console.log("mounted")
	},
	created(){
		console.log("created")
	},
	beforeCreate(){
		console.log("beforeCreate")
	},
	beforeMount(){
		console.log("beforeMount")
	},
	destroyed(){
		console.log("destroyed")
	},
	methods: {
		...mapMutations(["setSelectDevice"]),
		switchAddress(){
			uni.navigateTo({
				url:'../address/addressList'
			})
		},
		addDevice(){
			uni.navigateTo({
				url:'./addDevice'
			})
		},
		selectDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./deviceDetail'
			})
		},
		configDevice(device){
			this.$store.commit("setSelectDevice", device);
			uni.navigateTo({
				url:'./configDevice'
			})
		}
	}
}
